<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container-tab">
      <van-sticky class="tabs-sticky">
        <van-tabs class="van-tabs tabs" :active="active" swipeable animated sticky>
          <van-tab v-for="(item ,index) in tabs" :key="index" :title="item">
            <xmxx v-if="index == 0 && xmxxxq!=='' " :xmxxxq="xmxxxq" />
            <xmgs v-if="index == 1 && xmgs!=='' " :xmgs="xmgs" />
          </van-tab>
        </van-tabs>
      </van-sticky>
    </div>
  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar.vue'
import constants from '../../../../common/js/constants'
import xmxx from '../project/Xmxx'
import xmgs from '../project/Xmgs'
import { mapState } from 'vuex'
export default {
  name: 'Xmxxxq',
  components: {
    headerBar,
    xmxx,
    xmgs
  },

  data() {
    return {
      headerData: {
        title: '项目信息',
        leftUrl: 'back',
        leftText: ' '
      },
      ListItem: [],
      xmxxxq: '',
      xmgs: '',
      active: 0,
      tabs: ['项目信息', '项目概述']
    }
  },
  computed: {
    ...mapState({
      // userInfo: state => state.gysuser.gysUserInfo,
      // wybmListItem: state => state.gyswybm.wybmListItem
    })
  },

  created() {
    this.wdxmItem = this.$store.state.jbrwdxm.wdxmItem
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.$store.dispatch('jbrwdxm/getjbrXmjbxxAction', {
      id: this.wdxmItem.xmid
    }).then(res => {
      this.xmxxxq = res
    }).catch(error => {
      this.$toast.fail(error)
    })
    this.$store.dispatch('jbrwdxm/getjbrXmgsxxAction', {
      id: this.wdxmItem.xmid
    }).then(res => {
      this.xmgs = res
    }).catch(error => {
      this.$toast.fail(error)
    })
  },
  methods: {
  }
}
</script>

<style scoped>
.nav-bar{box-shadow: none;}
</style>
