<template>
  <div class="container-mini">
    <div class="content">
      <div class="common-main box">
        <div class="title">
          <div class="title-left">
            <h2>流转日志</h2>
          </div>
        </div>

        <div class="overviews-content">
          <div v-for="(item,index) in xmgs" :key="index" class="overviews-item">

            <div class="overviews-step" :class="getStatus(item.jddy_lczt)">
              <span class="num" />
              <div class="tit"><span>{{ item.jdmc }}</span></div>
            </div>
            <div class="overviews-box">
              <div class="item-data">
                <div v-for="(item1,index1) in item.lcsllist" :key="index1">
                  <div class="protitle">{{ item1.lcmc }}</div>
                  <ul>
                    <li><em>开始</em>{{ getKssj(item1.lcfqsj) }}</li>
                    <li><em>完成</em>{{ getJssj(item1.lcjssj) }}</li>
                    <li><em>计划</em>{{ getGzr(item1.gzr) }}个工作日</li>
                    <li><em>停留</em>{{ item1.wait }} </li>
                  </ul>
                  <div v-if="item1.lczt===1" class="state">未进行</div>
                  <div v-if="item1.lczt===2" class="state current">进行中</div>
                  <div v-if="item1.lczt===3" class="state done">已完成</div>
                  <div v-if="item1.rwslList.length>0">
                    <div :class="lcShow">
                      <em class="tit1" @click="isShow1()">展开流程
                        <i class="iconfont iconarrow-down" />
                      </em>
                    </div>
                  </div>
                  <div v-if="show">
                    <div v-if="item1.rwslList.length>0">
                      <div class="overviews-timeline active">
                        <div class="vertical-container-sprz">
                          <!-- <div class="vertical-timeline-block lcsp-done">
                            <div class="vertical-timeline-icon" />
                            <div class="vertical-timeline-content">
                              <h4>开始</h4>
                            </div>
                          </div> -->

                          <div v-for="(item2,index2) in item1.rwslList" :key="index2" class="vertical-timeline-block" :class="getStatus1(item2.rwzt)">
                            <div class="vertical-timeline-icon">
                              <i class="iconfont iconduihao" />
                            </div>
                            <div class="vertical-timeline-content">
                              <h4>{{ item2.rwmc }}</h4>

                              <div v-if="item2.zxrList.length>0" class="vertical-timeline-hide">
                                <div v-for="(item3,index3) in item2.zxrList" :key="index3">
                                  <p>
                                    <span class="sprz-name">{{ item3.rymc }}</span>
                                  <!-- <span class="sprz-info">流程审批</span> -->
                                  <!-- <span class="sprz-stay"> <em>停留</em>17天8小时</span> -->
                                  </p>
                                  <p>
                                    <span class="sprz-date"><em>送达时间</em> {{ getKssj(item3.rwcjsj) }} </span>
                                    <span class="sprz-date"><em>提交时间</em> {{ getJssj(item3.sjclsj) }}</span>
                                  </p>
                                  <div class="text-detail">
                                    <span class="sprz-title">审批意见：</span>{{ item3.clyj }}
                                  </div>
                                </div>
                              </div>

                            <!-- <div v-else>
                                  <div class="vertical-timeline-hide">
                                    <p>
                                      <span class="sprz-name" />
                                    </p>
                                    <p>
                                      <span class="sprz-date"><em>送达时间</em>  </span>
                                      <span class="sprz-date"><em>提交时间</em> </span>
                                    </p>
                                    <div class="text-detail">
                                      <span class="sprz-title">审批意见：</span>
                                    </div>
                                  </div>
                                </div> -->
                            </div>
                          </div>

                        </div>

                        <!-- <div class="vertical-timeline-block lcsp-undone">
                            <div class="vertical-timeline-icon" />
                            <div class="vertical-timeline-content">
                              <h4>结束</h4>
                            </div>
                          </div> -->
                        <div class="closebtn active">
                          <em class="tit2" @click="isShow2()">收起流程
                            <i class="iconfont iconarrow-up" />
                          </em>
                        </div>
                      </div>
                    <!-- </div> -->
                    </div>
                  </div>
                </div>
              </div>

            </div>

          </div>
        </div>
      </div>

    </div>
    <Backtop />
  </div>
</template>

<script land="script" scoped>

import constants from '@/common/js/constants'
import { formatTimes } from '@/common/js/util'
import { mapState, mapMutations } from 'vuex'
import Backtop from '@/components/backtop/backtop' // 返回顶部
// import { getYqhqrAction } from '../../../../store/gys/wybm'
// import popYqhck from '@/components/popup/pop-yqhck'
export default {
  name: 'Xmgs',
  components: {
    Backtop
    // popYqhck
  },
  props: {
    xmgs: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      // finished: true,
      swiperToNav: 0, // swiper滑动下标值传给nav导航
      status: {
        isShowPublish: false,
        topNum: 30
      },
      show: false,
      lcShow: 'closebtn'
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.gysuser.gysUserInfo
      // wybmListItem: state => state.wybm.wybmListItem,
      // gysyqk: state => state.wybm.yqhck
    })
  },
  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.SFZGYS = constants.SFZGYS
  },
  methods: {
    getGzr(gzr) {
      if (gzr === null) {
        return '-'
      } else {
        return gzr
      }
    },
    isShow1() {
      this.show = true
      this.lcShow = 'closebtn active'
    },
    isShow2() {
      this.show = false
      this.lcShow = 'closebtn'
    },
    onLoad() {

    },
    getStatus(jddy_lczt) {
      switch (jddy_lczt) {
        case 1: return 'undone'
        case 2: return 'current'
        case 3: return 'done'
      }
    },
    getStatus1(rwzt) {
      switch (rwzt) {
        case 1: return 'lcsp-undone'
        case 2: return 'lcsp-current'
        case 3: return 'lcsp-done'
      }
    },
    ...mapMutations([
      'SET_YQHZT'
    ]),
    getJssj(date) {
      if (date === null) {
        return null
      }
      var time = formatTimes(date)
      return time
    },
    getKssj(date) {
      console.log('转换前--------------', date)
      if (date === null) {
        return null
      }
      var time = formatTimes(date)
      console.log('转换后--------------', time)
      return time
    }
    // closeDialog() {
    //   if (this.fbxx.gysyqk !== null) {
    //     this.status.isShowPublish = false
    //   } else {
    //     this.$toast.fail('暂无数据！')
    //   }
    // }
  }
}
</script>

<style lang="css" scoped>
.vertical-timeline-hide{margin-bottom:10px;}
</style>
